@import url(https://fonts.googleapis.com/css2?family=Besley&display=swap);
html {
    scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
    background-color: #282c34;
    height: 100vh;
    font-size: calc(10px + 2vmin);
    color: white;
}

.full {
    height: 100%;
}

.header {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    padding-top: 30px;
    padding-bottom: 30px;
}

.header div {
    min-width: 300px;
}

.albumInfoDiv {
    width: 60%;
}

.searchBarDiv {
    width: 300px;
}

.title {
    width: 80%;
    text-align: center;
    padding: 30px 0px 30px 0px;
    margin: auto;
    border-bottom: 3px solid #ffffff40;
    font-size: 50px;
}

@media only screen and (max-width: 900px) {
    .header {
        flex-direction: column;
        align-items: center;
    }
    .header div {
        padding-bottom: 30px;
    }

    .title {
        text-align: center;
    }
}
/* import font */


/* hide submit button */
.searchBar .hidden {
    display: none;
}

.searchBar label {
    font-size: 28px;
    padding-right: 0px;
    display: block;
}

.searchBar input {
    width: 100%;
    background: transparent;
    border: 1px solid #ffffff40;
    border-radius: 5px;
    font-size: calc(10px + 2vmin);
    color: #d8c3c3;
}

.albumFlexbox {
    width: 80%;
    margin: auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    border-top: 3px solid #ffffff40;
    padding-top: 50px;
}

.albumFlexbox img {
    width: 100px;
    padding: 10px 10px 10px 10px;
}

.albumFlexbox div {
    width: 200px;
    height: 200px;
    flex-grow: 1;
    text-align: center;
}

.albumDetails .label {
    font-size: 14px;
    color: #a5a5a5;
}

.albumDetails .output {
    padding-bottom: 10px;
}

.albumDetails p {
    padding: 0px;
    margin: 0px;
}

.albumDetails {
    border-left: 0px solid white;
}

.url {
    color: #a0a0a0;
}
